import React from "react";

const DropdownMenu = ({ handleSelect }) => {
  return (
    <div className="text-center">
      <button
        className="text-center btn btn-custom dropdown-toggle btn-lg"
        type="button"
        id="dropdownMenuButton"
        data-toggle="dropdown"
        aria-haspopup="true"
        aria-expanded="false"
      >
        Selecciona un bloque
      </button>
      <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
        <button 
          className="dropdown-item" 
          onClick={() => handleSelect(1)}
          title="Aproximaciones, ordenacin, mltiplos y divisores"
          aria-label="Aproximaciones, ordenacin, mltiplos y divisores"
          tabIndex="0"
        >
          Bloque I - Nmeros y Operaciones
        </button>
        <button 
          className="dropdown-item" 
          onClick={() => handleSelect(2)}
          title="ngulos, monedas, horarios y otras medidas"
          aria-label="ngulos, monedas, horarios y otras medidas"
          tabIndex="0"
        >
          Bloque II - La Medida
        </button>
        <button 
          className="dropdown-item" 
          onClick={() => handleSelect(3)}
          title="Tipos de tringulos segn sus ngulos, reas y permetros"
          aria-label="Tipos de tringulos segn sus ngulos, reas y permetros"
          tabIndex="0"
        >
          Bloque III - Formas geomtricas y Orientacin Espacial
        </button>
        <button 
          className="dropdown-item" 
          onClick={() => handleSelect(4)}
          title="Medias, modas, medianas, mximos, mnimos y grficas"
          aria-label="Medias, modas, medianas, mximos, mnimos y grficas"
          tabIndex="0"
        >
          Bloque IV - Organizacin de la Informacin
        </button>
      </div>
    </div>
  );
};

export default DropdownMenu;

